//Шаблонні

@mixin mobile {
  @media screen and (min-width: 390px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (min-width: 870px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: 1200px) {
    @content;
  }
}

